import React, { useEffect, useState } from 'react';
import ReactEcharts from 'echarts-for-react';
import 'echarts/lib/chart/bar';
import dayjs from 'dayjs'
import moon from '@/assets/icon/moon.svg'


function MyChart({ charData }) {

  const [option, setOption] = useState({
    backgroundColor: 'rgba(0,0,0,0)',

  })
  const init = () => {


    if (charData?.seriesdata?.length > 0) {

      let data = option.series
      setOption({
        backgroundColor: 'rgba(0,0,0,0)',
        grid: {
          left: '0',
          right: '4%',
          bottom: '3%',
          top: '10%',
          containLabel: true
        },
        xAxis: {
          type: 'time',
          min: '2022-01-01 00:00', // 设定起始时间点，使用具体日期加上时间
          max: '2022-01-01 23:59', // 设定结束时间点
          axisLabel: {
            formatter: function (value) {
              // 使用 dayjs 或其他日期库来格式化时间标签
              return dayjs(value).format('HH:mm');
            }
          },
          splitLine: {
            show: false // 如果不希望显示网格线，可以将其关闭
          },

          axisTick: { show: false },

          boundaryGap: false // 这样设置会让第一个点靠近Y轴
        },
        yAxis: {
          show: false,
          name: 'bpm', // 添加单位名称
          nameLocation: 'end', // 单位名称显示的位置，'end' 表示在轴的末尾
          nameTextStyle: { // 定制单位文本的样式
            color: '#333',
            fontSize: 12,
            padding: [0, 0, 10, -10] // 单位名称距离轴线的内边距，顺序为上右下左
          },
          splitLine: { show: false },
          axisLine: { show: true },
          type: 'value',
          axisLabel: {
            color: "#000",
          },
          max: "900"
        },
        series: [
          {
            data: charData.seriesdata,
            type: 'line',
            color: charData.color,
            areaStyle: {},

            showSymbol: false, // 不显示折线图的数据点标记
            markArea: {
              itemStyle: {
                color: '#E6E6F6'
              },
              data: [
                [
                  {
                    name: '',
                    xAxis: '2022-01-01 18:00'
                  },
                  {
                    xAxis: '2022-01-01 23:59'
                  }
                ],

              ]
            }
          }
        ],
        graphic: [
          {
            type: 'image',
            id: 'mySVG',
            right: '5%', // 图形的定位
            top: '12%',  // 图形的定位
            z: 10, // 控制图层，确保在最上层
            bounding: 'raw', // 使用图形元素的自身的宽高进行定位
            style: {
              image: moon, // SVG 图像的 URL
              width: 19, // 设置 SVG 图像的宽度
              height: 19, // 设置 SVG 图像的高度
              opacity: 1, // 设置透明度
            }
          }
        ]
      })

    } else {
      setOption({
        title: {
          text: "There is currently no data available",
          show: true,
          left: "center",
          top: "center",
          textStyle: {
            color: "#000",
            fontSize: "20px",
            fontWeight: 400,
          },
        },
      })
    }

  }


  useEffect(() => {
    init()
  }, [charData])



  return <ReactEcharts option={option} notMerge={true} />;
}

export default MyChart;