import React, { useEffect, useState } from 'react';
import ReactEcharts from 'echarts-for-react';
import 'echarts/lib/chart/bar';
import dayjs from 'dayjs'
import moon from '@/assets/icon/moon.svg'


function WeekSosChar({ charData }) {

    const [option, setOption] = useState({



    })
    const init = () => {
        // const colorList = [
        //     '#4f81bd',
        //     '#c0504d',
        //     '#9bbb59',
        //     '#604a7b',
        //     '#948a54',
        //     '#e46c0b'
        // ];
        // const data = [
        //     [5, 0, ],
        //     [1, 15],

        // ].map(function (item, index) {
        //     return {
        //         value: item,
        //         itemStyle: {
        //             color: colorList[index]
        //         }
        //     };
        // });
        // console.log("Data",data)

        if (charData?.seriesdata?.xdata?.length > 0) {
            const { values } = charData.seriesdata

            setOption({
                backgroundColor: 'rgba(0,0,0,0)',
                grid: {
                    left: '0',
                    right: '4%',
                    bottom: '3%',
                    top: '12%',
                    containLabel: true
                },
                xAxis: {
                    type: 'category',
                    // data: xdata,
                    splitLine: {
                        show: false // 如果不希望显示网格线，可以将其关闭
                    },

                    axisTick: { show: false },

                    // boundaryGap: false // 这样设置会让第一个点靠近Y轴
                },
                yAxis: {
                    show: true,
                    name: 'bpm', // 添加单位名称
                    nameLocation: 'end', // 单位名称显示的位置，'end' 表示在轴的末尾
                    nameTextStyle: { // 定制单位文本的样式
                        color: '#333',
                        fontSize: 12,
                        padding: [0, 0, 10, -10] // 单位名称距离轴线的内边距，顺序为上右下左
                    },
                    splitLine: { show: false },
                    axisLine: { show: true },
                    type: 'value',
                    axisLabel: {
                        color: "#000",
                    },

                },
                series: [
                    {
                        type: 'custom',
                        renderItem: function (params, api) {
                            var yValue = api.value(1);
                            var start = api.coord([api.value(0), yValue]);
                            var size = api.size([api.value(1) - api.value(0), yValue]);
                            var style = api.style();
                            var barWidth = 20; // 设置柱子的宽度为20像素
                            var borderRadius = 10; // 柱子圆角半径
                            console.log('yValue', yValue)
                            if (yValue === 0) {
                                return {
                                    type: 'group',
                                    children: [{
                                        type: 'circle', // 绘制圆形
                                        shape: {
                                            cx: start[0], // 圆心x坐标
                                            cy: start[1] - 10, // 圆心y坐标稍微向下调整，确保圆球在柱子下方
                                            r: 10 // 半径
                                        },
                                        style: style
                                    }]
                                };
                            }
                            return {
                                type: 'rect',
                                shape: {
                                    x: start[0] - barWidth / 2,
                                    y: start[1],
                                    width: barWidth,
                                    height: size[1],
                                    r: borderRadius // 设置圆角
                                },
                                style: style
                            };
                        },

                        dimensions: ['x', 'y'],
                        encode: {
                            x: 'x',
                            y: ["y"],
                        },
                        data: values
                    }

                ],

            })

        } else {

            setOption({
                title: {
                    text: "There is currently no data available",
                    show: true,
                    left: "center",
                    top: "center",
                    textStyle: {
                        color: "#000",
                        fontSize: "20px",
                        fontWeight: 400,
                    },
                },
            })

        }

    }


    useEffect(() => {
        init()
    }, [charData])



    return <ReactEcharts option={option} notMerge={true} />;
}

export default WeekSosChar;